/*
Reset.css
Updated: 2021-08-25
Author: Youni
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
button, input, select, textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
}

button, input {
  line-height: normal;
}

:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul, li {
  list-style: none;
}

li {
  display: list-item;
}

table {
  display: table;
  border-collapse: collapse;
  border-spacing: 0;
}

tr {
  display: table-row;
}

thead {
  display: table-header-group;
}

tbody {
  display: table-row-group;
}

tfoot {
  display: table-footer-group;
}

col {
  display: table-column;
}

colgroup {
  display: table-column-group;
}

td,
th {
  display: table-cell;
}

caption {
  display: table-caption;
}

th {
  font-weight: bolder;
  text-align: center;
}

caption {
  text-align: center;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

a {
  text-decoration: none;
}

a:focus,
a:active,
a:hover {
  outline: 0;
}

h1, h2, h3, h4, h5, h6,
b, strong {
  font-weight: bolder;
}

img {
  margin: 0;
  padding: 0;
  border: 0;
}

/* Begin bidirectionality settings (do not change) */
BDO[DIR=ltr] {
  direction: ltr;
  unicode-bidi: bidi-override;
}

BDO[DIR=rtl] {
  direction: rtl;
  unicode-bidi: bidi-override;
}

*[DIR=ltr] {
  direction: ltr;
  unicode-bidi: embed;
}

*[DIR=rtl] {
  direction: rtl;
  unicode-bidi: embed;
}

@media print {
  h1 {
    page-break-before: always;
  }

  h1, h2, h3, h4, h5, h6 {
    page-break-after: avoid;
  }

  ul, ol, dl {
    page-break-before: avoid;
  }
}
::-webkit-scrollbar {
  width: 12px;
  height: 8px;
  border: 3px solid #ffffff;
}

::-webkit-scrollbar-button:start:decrement,
::-webkit-scrollbar-button:end:increment {
  display: block;
  height: 10px;
  background: #ffffff;
}

::-webkit-scrollbar-track {
  background: #ffffff;
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  height: 50px;
  width: 50px;
  background: #ff8955;
  -webkit-border-radius: 8px;
  border-radius: 8px;
}

.v-application {
  font-family: "Maplestory", sans-serif !important;
}

@font-face {
  font-family: "Maplestory";
  font-weight: normal;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.eot?#iefix") format("embedded-opentype"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.woff2") format("woff2"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.woff") format("woff"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Maplestory";
  font-weight: bold;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.eot?#iefix") format("embedded-opentype"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.woff2") format("woff2"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.woff") format("woff"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.ttf") format("truetype");
  font-display: swap;
}
div {
  font-family: "Maplestory";
  color: #5e5e5e;
}

a {
  color: #5e5e5e;
}

a:link {
  color: #5e5e5e;
}

a:visited {
  color: #5e5e5e;
}

a:active {
  color: #5e5e5e;
}

#app {
  text-align: center;
  color: #5e5e5e;
  background: #fff;
}

#nav {
  border-bottom: 1px solid #e4e4e4;
  position: fixed;
  z-index: 50;
  height: 50px;
  width: 100%;
  background: #fff;
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  font-size: 1em;
  line-height: 30px;
}
#nav a {
  font-weight: bold;
  text-decoration: none;
  color: #5e5e5e;
}
#nav > div {
  padding: 0 20px;
}

#nav #logo {
  background: url(./assets/images/logo.png) right no-repeat;
  background-size: contain;
  width: 70px;
  height: 40px;
  text-indent: -9999px;
}

@media all and (min-width: 768px) {
  #nav {
    position: fixed;
    justify-content: space-between;
    z-index: 50;
    height: 60px;
    width: 700px;
    left: 50%;
    margin-left: -350px;
    background: #fff;
    display: -webkit-flex;
    padding: 10px 0;
  }
  #nav a {
    font-weight: bold;
    color: #5e5e5e;
    text-decoration: none;
    line-height: 40px;
  }
  #nav a:hover {
    color: #ff8955;
  }
  #nav #logo {
    background: url(./assets/images/logo.png) right no-repeat;
    background-position: 20px 20px;
    width: 70px;
    height: 60px;
    text-indent: -9999px;
    margin-top: -10px;
    background-size: 70%;
  }
  #nav #logo:hover {
    animation-duration: 1s;
    animation-name: slidein;
  }
  @keyframes slidein {
    from {
      background-position: 20px 20px;
    }
    50% {
      background-position: 20px 10px;
    }
    to {
      background-position: 20px 20px;
    }
  }
}