@charset "UTF-8";
/*
Reset.css
Updated: 2021-08-25
Author: Youni
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
button, input, select, textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
}

button, input {
  line-height: normal;
}

:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul, li {
  list-style: none;
}

li {
  display: list-item;
}

table {
  display: table;
  border-collapse: collapse;
  border-spacing: 0;
}

tr {
  display: table-row;
}

thead {
  display: table-header-group;
}

tbody {
  display: table-row-group;
}

tfoot {
  display: table-footer-group;
}

col {
  display: table-column;
}

colgroup {
  display: table-column-group;
}

td,
th {
  display: table-cell;
}

caption {
  display: table-caption;
}

th {
  font-weight: bolder;
  text-align: center;
}

caption {
  text-align: center;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

a {
  text-decoration: none;
}

a:focus,
a:active,
a:hover {
  outline: 0;
}

h1, h2, h3, h4, h5, h6,
b, strong {
  font-weight: bolder;
}

img {
  margin: 0;
  padding: 0;
  border: 0;
}

/* Begin bidirectionality settings (do not change) */
BDO[DIR=ltr] {
  direction: ltr;
  unicode-bidi: bidi-override;
}

BDO[DIR=rtl] {
  direction: rtl;
  unicode-bidi: bidi-override;
}

*[DIR=ltr] {
  direction: ltr;
  unicode-bidi: embed;
}

*[DIR=rtl] {
  direction: rtl;
  unicode-bidi: embed;
}

@media print {
  h1 {
    page-break-before: always;
  }

  h1, h2, h3, h4, h5, h6 {
    page-break-after: avoid;
  }

  ul, ol, dl {
    page-break-before: avoid;
  }
}
::-webkit-scrollbar {
  width: 12px;
  height: 8px;
  border: 3px solid #ffffff;
}

::-webkit-scrollbar-button:start:decrement,
::-webkit-scrollbar-button:end:increment {
  display: block;
  height: 10px;
  background: #ffffff;
}

::-webkit-scrollbar-track {
  background: #ffffff;
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  height: 50px;
  width: 50px;
  background: #ff8955;
  -webkit-border-radius: 8px;
  border-radius: 8px;
}

.v-application {
  font-family: "Maplestory", sans-serif !important;
}

@font-face {
  font-family: "Maplestory";
  font-weight: normal;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.eot?#iefix") format("embedded-opentype"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.woff2") format("woff2"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.woff") format("woff"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Maplestory";
  font-weight: bold;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.eot?#iefix") format("embedded-opentype"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.woff2") format("woff2"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.woff") format("woff"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.ttf") format("truetype");
  font-display: swap;
}
div {
  font-family: "Maplestory";
  color: #5e5e5e;
}

a {
  color: #5e5e5e;
}

a:link {
  color: #5e5e5e;
}

a:visited {
  color: #5e5e5e;
}

a:active {
  color: #5e5e5e;
}

.body {
  background: #eee;
  height: 100%;
  min-height: 100vh;
}

.top {
  position: fixed;
  margin: 0 auto;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 10;
  border: 1px solid #eeeeee;
  background: #fff;
}

.rank,
.fuel {
  font-size: 0.7em;
}
.rank .sub,
.fuel .sub {
  color: #cf5724;
  display: block;
  margin-bottom: 4px;
}
.rank > div,
.fuel > div {
  width: 76px;
  float: left;
  margin: auto;
  line-height: 2em;
  display: flex;
}
.rank > div input,
.fuel > div input {
  margin: 0 5px;
  float: left;
  -webkit-appearance: none;
  position: relative;
  cursor: pointer;
  outline: none !important;
  border: 1px solid #eeeeee;
  border-radius: 2px;
  background: #fbfbfb;
}
.rank > div input[type=checkbox]::before,
.fuel > div input[type=checkbox]::before {
  content: "✓";
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: scale(0) translate(-50%, -50%);
  line-height: 1;
}
.rank > div input[type=checkbox]:checked,
.fuel > div input[type=checkbox]:checked {
  background-color: #ff8955;
  border-color: #ffffff4d;
  color: #fff;
}
.rank > div input[type=checkbox]:checked::before,
.fuel > div input[type=checkbox]:checked::before {
  border-radius: 2px;
  transform: scale(1) translate(-50%, -50%);
}
.rank .sort li,
.fuel .sort li {
  margin: 0 5px;
}
.rank .sort li a,
.fuel .sort li a {
  color: #5e5e5e;
}
.rank .sort li a:link,
.fuel .sort li a:link {
  color: #5e5e5e;
}
.rank .sort li a:visited,
.fuel .sort li a:visited {
  color: #5e5e5e;
}
.rank .sort li a:active,
.fuel .sort li a:active {
  color: #5e5e5e;
}

.search_box #search {
  background: #e4e4e4;
  border: 1px solid #5e5e5e;
  border-left: 0;
  width: 50px;
  height: 30px;
  line-height: 30px;
}
.search_box #reset {
  line-height: 30px;
}
.search_box .contents_list {
  width: 701px;
  margin: auto;
  padding-top: 224px;
  border: 1px solid #eeeeee;
  background-color: #ffffff;
  border-top: 0;
}

@media all and (max-width: 767px) {
  .top {
    width: 100%;
    padding: 20px;
    padding-top: 60px;
    height: 280px;
  }

  .filter {
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
  }
  .filter > div {
    width: 50%;
  }

  .no:nth-of-type(2n) {
    border: 0;
  }

  .rank .sub,
.fuel .sub {
    font-size: 1.6em;
    text-align: center;
    line-height: 1.8em;
  }
  .rank > div,
.fuel > div {
    border-right: 1px solid #e4e4e4;
    font-size: 1.1em;
    line-height: 12px;
  }
  .rank > div input,
.fuel > div input {
    margin-bottom: 10px;
    width: 12px;
    height: 12px;
  }

  .sort {
    flex-direction: column;
    font-size: 0.8em;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
  }
  .sort ul {
    display: flex;
    width: 50%;
    justify-content: center;
    line-height: 3em;
    font-size: 1.1em;
  }
  .sort ul li a {
    padding: 0 10px;
  }
  .sort .search_box {
    padding-top: 5px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
  }
  .sort .search_box .stage-search {
    width: 70%;
    font-size: 1em;
    line-height: 1.4em;
  }

  .contents_list {
    width: 100%;
    margin: auto;
    padding-top: 260px;
    border: 1px solid #eeeeee;
    background-color: #ffffff;
    border-top: 0;
  }

  .contents {
    background: #fff;
    border: 1px solid #e4e4e4;
    display: flex;
    margin: auto;
    flex-wrap: wrap;
    flex-flow: column;
  }
  .contents .img_box {
    width: 100%;
    order: 0;
    overflow: hidden;
    background: #fff;
  }
  .contents .img_box img {
    height: 150px;
  }
  .contents .contents_box {
    display: flex;
    flex-wrap: wrap;
    height: 140px;
    width: 100%;
    justify-content: space-between;
  }
  .contents .contents_box .c_box_01 {
    order: 1;
    text-align: left;
    margin-left: 20px;
  }
  .contents .contents_box .c_box_01 .rank_name {
    font-weight: bold;
    font-size: 1.4em;
  }
  .contents .contents_box .c_box_01 dd {
    font-size: 1em;
    line-height: 1.4em;
  }
  .contents .contents_box .c_box_02 {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    order: 2;
    text-align: right;
  }
  .contents .contents_box .c_box_02 .car_rank {
    padding: 10px 20px;
    display: block;
    color: #fff;
    font-size: 1.2em;
    background: #ff8955;
  }
  .contents .contents_box .c_box_02 .star {
    font-size: 1em;
    margin: 10px 20px;
    color: #ff8955;
  }
  .contents .contents_box .c_box_03 {
    position: relative;
    order: 3;
    width: 100%;
    text-align: right;
    align-self: flex-end;
    font-size: 2em;
    color: #cf5724;
  }
  .contents .contents_box .c_box_03 button {
    font-size: 0.6em;
    line-height: 2em;
    padding: 0 20px;
    border: 0;
    color: #fff;
    background: #cf5724;
  }
  .contents .contents_box .c_box_03 button:hover {
    background: #ff8955;
  }
}
@media all and (min-width: 768px) {
  .no {
    border: 0;
  }

  .top {
    width: 701px;
    padding: 20px;
    padding-top: 80px;
  }

  .filter {
    display: flex;
  }
  .filter > div {
    width: 47%;
    margin-left: 3%;
  }

  .rank .sub,
.fuel .sub {
    margin-left: 5px;
    font-size: 1.8em;
    text-align: left;
    margin-bottom: 10px;
  }
  .rank > div,
.fuel > div {
    font-size: 1em;
    line-height: 16px;
  }
  .rank > div input,
.fuel > div input {
    width: 16px;
    height: 16px;
    margin-bottom: 8px;
  }

  .sort {
    display: flex;
    justify-content: space-between;
    height: 30px;
    margin-left: 3%;
    flex-direction: row;
  }
  .sort a {
    color: #5e5e5e;
    padding: 0 7px;
  }
  .sort ul {
    display: flex;
    justify-content: flex-start;
    line-height: 40px;
    width: 30%;
  }
  .sort ul li a:hover {
    cursor: pointer;
    color: #cf5724;
  }
  .sort .search_box {
    width: 45%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }
  .sort .search_box .stage-search {
    font-size: 1em;
    line-height: 1.4em;
    width: 70%;
    text-align: left;
  }
  .sort .search_box #search:hover {
    background: #ff8955;
    color: #fff;
    border-left: 0;
    border: 1px solid #cf5724;
  }
  .sort .search_box #reset:hover {
    color: #cf5724;
  }

  .contents_list {
    width: 702px;
    margin: auto;
    padding-top: 220px;
    border: 1px solid #eeeeee;
    background-color: #ffffff;
    border-top: 0;
  }

  .contents {
    background: #fff;
    border: 1px solid #e4e4e4;
    display: flex;
    width: 701px;
    height: 200px;
    margin: auto;
  }
  .contents .img_box {
    width: 250px;
    height: 200px;
    margin-right: 20px;
    order: 0;
    overflow: hidden;
    background: #fff;
  }
  .contents .img_box img {
    width: 100%;
  }
  .contents .contents_box {
    display: flex;
    flex-wrap: wrap;
    height: 200px;
    width: 450px;
    justify-content: space-between;
  }
  .contents .contents_box .c_box_01 {
    order: 1;
    text-align: left;
  }
  .contents .contents_box .c_box_01 .rank_name {
    font-weight: bold;
    font-size: 2em;
    line-height: 2em;
  }
  .contents .contents_box .c_box_01 dd {
    font-size: 1.2em;
    line-height: 1.6em;
  }
  .contents .contents_box .c_box_02 {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    order: 2;
    text-align: right;
  }
  .contents .contents_box .c_box_02 .car_rank {
    padding: 10px 20px;
    display: block;
    color: #fff;
    font-size: 1.6em;
    background: #ff8955;
  }
  .contents .contents_box .c_box_02 .star {
    font-size: 1.4em;
    margin: 10px 20px;
    color: #ff8955;
  }
  .contents .contents_box .c_box_03 {
    position: relative;
    top: -1px;
    order: 3;
    width: 100%;
    text-align: right;
    align-self: flex-end;
    font-size: 3em;
    color: #cf5724;
  }
  .contents .contents_box .c_box_03 button {
    font-size: 0.6em;
    line-height: 2em;
    padding: 0 20px;
    border: 0;
    color: #fff;
    background: #cf5724;
  }
  .contents .contents_box .c_box_03 button:hover {
    background: #ff8955;
  }
}