/*
Reset.css
Updated: 2021-08-25
Author: Youni
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
button, input, select, textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
}

button, input {
  line-height: normal;
}

:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul, li {
  list-style: none;
}

li {
  display: list-item;
}

table {
  display: table;
  border-collapse: collapse;
  border-spacing: 0;
}

tr {
  display: table-row;
}

thead {
  display: table-header-group;
}

tbody {
  display: table-row-group;
}

tfoot {
  display: table-footer-group;
}

col {
  display: table-column;
}

colgroup {
  display: table-column-group;
}

td,
th {
  display: table-cell;
}

caption {
  display: table-caption;
}

th {
  font-weight: bolder;
  text-align: center;
}

caption {
  text-align: center;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

a {
  text-decoration: none;
}

a:focus,
a:active,
a:hover {
  outline: 0;
}

h1, h2, h3, h4, h5, h6,
b, strong {
  font-weight: bolder;
}

img {
  margin: 0;
  padding: 0;
  border: 0;
}

/* Begin bidirectionality settings (do not change) */
BDO[DIR=ltr] {
  direction: ltr;
  unicode-bidi: bidi-override;
}

BDO[DIR=rtl] {
  direction: rtl;
  unicode-bidi: bidi-override;
}

*[DIR=ltr] {
  direction: ltr;
  unicode-bidi: embed;
}

*[DIR=rtl] {
  direction: rtl;
  unicode-bidi: embed;
}

@media print {
  h1 {
    page-break-before: always;
  }

  h1, h2, h3, h4, h5, h6 {
    page-break-after: avoid;
  }

  ul, ol, dl {
    page-break-before: avoid;
  }
}
::-webkit-scrollbar {
  width: 12px;
  height: 8px;
  border: 3px solid #ffffff;
}

::-webkit-scrollbar-button:start:decrement,
::-webkit-scrollbar-button:end:increment {
  display: block;
  height: 10px;
  background: #ffffff;
}

::-webkit-scrollbar-track {
  background: #ffffff;
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  height: 50px;
  width: 50px;
  background: #ff8955;
  -webkit-border-radius: 8px;
  border-radius: 8px;
}

.v-application {
  font-family: "Maplestory", sans-serif !important;
}

@font-face {
  font-family: "Maplestory";
  font-weight: normal;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.eot?#iefix") format("embedded-opentype"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.woff2") format("woff2"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.woff") format("woff"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Maplestory";
  font-weight: bold;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.eot?#iefix") format("embedded-opentype"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.woff2") format("woff2"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.woff") format("woff"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.ttf") format("truetype");
  font-display: swap;
}
div {
  font-family: "Maplestory";
  color: #5e5e5e;
}

a {
  color: #5e5e5e;
}

a:link {
  color: #5e5e5e;
}

a:visited {
  color: #5e5e5e;
}

a:active {
  color: #5e5e5e;
}

.body,
.moun,
.road,
.ocean,
.back,
.forest {
  width: 100vw;
  overflow: hidden;
  min-width: 100vw;
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}

.forest ::-webkit-scrollbar,
body::-webkit-scrollbar {
  display: none;
  width: 0 !important;
}

p {
  position: fixed;
  font-weight: bold;
  color: #ff8955;
  left: 50%;
  top: 20%;
  z-index: 90;
  margin: auto;
  margin-left: -90px;
  font-size: 2em;
}

.body {
  width: 100vw;
  height: 90vh;
}

.back {
  position: fixed;
  z-index: 50;
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background: linear-gradient(to bottom, #f0faff, #a2e1ff);
}

.forest {
  position: absolute;
  left: 0;
  top: 0%;
  height: 100vh;
  width: 100vw;
}
.forest .far,
.forest .mid,
.forest .near {
  z-index: 58;
  animation: move-forever 12s linear infinite;
  position: absolute;
  width: 900vw;
  background-repeat: repeat-x;
  animation: slide linear infinite;
  background-size: contain;
}
.forest .far {
  background: url("../../assets/images/go/forest1.svg") center repeat-x;
  bottom: 240px;
  height: 100px;
  animation-duration: 60s;
}
.forest .mid {
  background: url("../../assets/images/go/forest2.svg") top repeat-x;
  bottom: 242px;
  height: 70px;
  animation-duration: 45s;
}
.forest .near {
  background: url("../../assets/images/go/forest3.svg") center repeat-x;
  height: 50px;
  bottom: 240px;
  animation-duration: 30s;
}

@keyframes slide {
  0% {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(-3000px, 0, 0);
  }
}
.moun {
  z-index: 58;
  height: 300px;
  position: fixed;
  bottom: 198px;
  background: url(../../assets/images/go/moun.png) center no-repeat;
  background-size: contain;
}

.sun {
  z-index: 68;
  width: 70px;
  height: 70px;
  position: absolute;
  top: 130px;
  right: 20%;
  background: #ffde4a;
  border-radius: 50px;
}

.road {
  z-index: 65;
  width: 100%;
  height: 200px;
  background-color: #fffcd3;
  position: absolute;
  bottom: 40px;
  padding: 0;
}

.ocean {
  z-index: 70;
  position: absolute;
  margin: 0;
  max-height: 100vh;
  bottom: 0;
  overflow: hidden;
  padding: 9em 0 0 0;
}
.ocean .parallax > use {
  animation: move-forever 12s linear infinite;
}
.ocean .parallax > use:nth-child(1) {
  animation-delay: -2s;
}
.ocean .parallax > use:nth-child(2) {
  animation-delay: -2s;
  animation-duration: 5s;
}
.ocean .parallax > use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 3s;
}
@keyframes move-forever {
  0% {
    transform: translate(-90px, 0%);
  }
  100% {
    transform: translate(85px, 0%);
  }
}
.ocean .editorial {
  display: block;
  width: 100%;
  height: 10em;
  max-height: 100vh;
  margin: 0;
}

.car {
  z-index: 90;
  left: 50%;
  margin-left: -100px;
  bottom: 170px;
  position: absolute;
  background: url("../../assets/images/go/move.gif") center repeat-x;
  background-size: contain;
  width: 200px;
  height: 200px;
  transition-duration: 1s;
}

.car:hover {
  cursor: pointer;
  width: 230px;
  height: 230px;
}

@media all and (max-width: 767px) {
  p {
    position: fixed;
    font-weight: bold;
    color: #ff8955;
    left: 30%;
    top: 30%;
    z-index: 90;
    margin: auto;
    font-size: 2em;
  }

  .sun {
    width: 70px;
    height: 70px;
    top: 130px;
    left: 50%;
    margin-left: -35px;
  }

  .far {
    height: 80px;
  }
}